import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";

@Injectable({
  providedIn: 'root'
})
export class HomeService {
  private baseUrl = environment.apiUrl;
  constructor(private http: HttpClient) { }

  paymentLists(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String, isToday: boolean,) {
    let url: any;
    if (isToday==false)
      if (searchData) url = this.baseUrl + "/payments?search=" + searchData;
      else
        url =
          this.baseUrl +
          "/payments?page=" +
          page +
          "&limit=" +
          limt +
          "&sortColumn=" +
          sortColumn +
          "&sortDirection=" +
          SortDirection;
    else if (searchData) url = this.baseUrl + "/payments?today="+isToday+ "&search=" + searchData;
    else
      url =
        this.baseUrl +
        "/payments?today="+isToday+"&page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;
    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  createPayment(payment) {
    let url = this.baseUrl + "/payments";
    return this.http.post(url, payment)
  }

  editPayment(payment, paymentId) {
    let url = this.baseUrl + "/payments/" + paymentId;
    return this.http.put(url, payment)
  }

  getPayemntDetail(id: String) {
    let url = this.baseUrl + "/payments/" + id;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }
}
