import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActionLabel, Label } from '@app/enum/app_enum';
import { HomeService } from "@core/services/home.service";
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {
  public subscription: Subscription;

  public tabLists: any = [ActionLabel.paymentInfo, ActionLabel.todayPayment];
  public activeIndex: any;
  public paymentLists: any = [];
  public totalCount: any;
  public tableHeader = [Label.createDate, Label.accountType, Label.payAccountType, Label.status, Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete, ActionLabel.view];
  public objectLabel: any = ["createdDate", "accountType", "payAccountType", "status"];
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public searchData: any;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public selectedPayment: any = {};
  public isCreate: boolean = false;
  public actionLinks: any = {
    editLink: "",
    deleteLink: "payments",
    idKeyname: "paymentId",
    isModal: true,
  }

  constructor(private _service: HomeService) { }

  ngOnInit(): void {
  }
  public isToday: boolean = true;
  tabChange(index: any) {
    console.log(index);
    this.isToday = index == 1 ? true : false;
    console.log(this.isToday);
    this.activeIndex = index;
    this.getPaymentLists();

  }

  getPaymentLists() {
    this._service.paymentLists(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection, this.isToday)
      .subscribe((res) => {
        this.paymentLists.data = res.payments;
        this.totalCount = this.paymentLists.data.length;
        this.paymentLists.btnLink = this.actionLinks;
        this.paymentLists.tableHeader = this.tableHeader;
        this.paymentLists.actionBtns = this.actionBtns;
        this.paymentLists.objectLabel = ["createdDate", "accountType", "payAccountType", "status"];
      });

  }

  pageChanges(e: any) {
    this.page = e;
    this.getPaymentLists();
  }

  onFilter(filter: {}) {
    this.filter = filter;
    this.getPaymentLists();
  }

  onsearch(val) {
    this.searchData = val;
    this.getPaymentLists();
  }

  onSave() {
    this.isCreate = false;
    this.editId = undefined;
    this.getPaymentLists();
  }

  public editId: any;
  onEdit(data) {
    this.editId = data;
    this.isCreate = true;

  }
  ngOnDestroy() {
    if(this.subscription)
    this.subscription.unsubscribe();
  }
}
